import { Component } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';

import { CourseService } from './courses/course.service';
import { ProfService } from './profs/prof.service';
import { RoomService } from './rooms/room.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Schedule generator';
  url = './generate/';
  constructor(private http: Http, private courseService: CourseService, private profService: ProfService, private roomService: RoomService) {}
  submit() {
    var courses = this.courseService.getCourses();
    var profs = this.profService.getProfs();
    var rooms = this.roomService.getRooms();

    var raw = {"courses": courses, "profs": profs, "rooms": rooms};
    console.log(raw);

    var json = JSON.stringify(raw);
    console.log(json);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.url, json, options).map(this.extractData);
  }
  generate() {



    //console.log(JSON.stringify(courseService.getCourses()));

    //console.log(this.url);

    this.submit().subscribe(b => console.log(b));
  }
  private extractData(res: Response) {
    console.log("WTF");
	   let body = res.json();
     console.log(body);
     return body || {};
  }
  private handleErrorObservable (error: Response | any) {
	   console.error(error.message || error);
     return Observable.throw(error.message || error);
  }
}



