import { Injectable } from '@angular/core';
import { Prof } from './prof';

@Injectable()
export class ProfService {
  profs: Prof[] = [new Prof(1)];

  constructor() { }

  getProfs(): Prof[] {
    return this.profs;
  }

  addProf(): Prof {
    this.profs.push(new Prof(this.profs.length + 1));
    return this.profs[this.profs.length - 1];
  }
}



