import { Course } from '../courses/course';

export class Prof {
  id: number;
  name: string;
  rank: number;
  coursesAvailable: number[];
  isAvailable: boolean[];
  constructor(id: number) {
    this.id = id;
    this.name = "New professor (" + this.id + ")";
    this.rank = 0;
    this.isAvailable = [];
    for (var i = 0; i < 30; i++)
      this.isAvailable.push(false);
    this.coursesAvailable = [];
  }
}



