import { Component, OnInit } from '@angular/core';
import { ProfService } from '../prof.service';
import { Prof } from '../prof';

@Component({
  selector: 'app-prof-list',
  templateUrl: './prof-list.component.html',
  styleUrls: ['./prof-list.component.css']
})
export class ProfListComponent implements OnInit {
  profs: Prof[] = [];
  selectedProf: Prof;

  constructor(private profService: ProfService) { }

  ngOnInit() {
    this.getProfs();
    if (this.profs.length > 0) {
      this.selectedProf = this.profs[0];
    }
  }

  getProfs() {
    this.profs = this.profService.getProfs();
  }

  addProf() {
    this.selectedProf = this.profService.addProf();
  }

  selectProf(prof: Prof) {
    this.selectedProf = prof;
  }
}



