import { Injectable } from '@angular/core';
import { Room } from './room';

@Injectable()
export class RoomService {
  rooms: Room[] = [{
    name: "New room (1)",
    type: "lecture",
    capacity: 0
  }];
  constructor() { }

  getRooms() {
    return this.rooms;
  }

  addRoom(): Room {
    let id = this.rooms.length + 1;
    this.rooms.push({
      name: "New room (" + id + ")",
      type: "lecture",
      capacity: 0
    });
    return this.rooms[this.rooms.length - 1];
  }
}



