import { Injectable } from '@angular/core';
import { Course } from './course';

@Injectable()
export class CourseService {
  courses: Course[] = [
    {
      id: 1,
      name: 'New course (1)',
      classList: [],
      capacity: 0,
      grade: "freshman",
      sectionCnt: 0
    }
  ];

  constructor() { }

  getCourses(): Course[] {
    return this.courses;
  }

  addCourse(): Course {
    let id = this.courses.length + 1;
    this.courses.push({
      id: id,
      name: 'New course (' + id + ')',
      classList: [],
      capacity: 0,
      grade: "freshman",
      sectionCnt: 0
    });
    return this.courses[this.courses.length - 1];
  }

  addClass(course): void {
    console.log(course);
    for (var i = 0; i < this.courses.length; i++) {
      if (this.courses[i].id == course.id)
        this.courses[i].classList.push({
          type: 'lecture',
          duration: 75
        });
    }
  }
}



