import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http'

import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CourseListComponent } from './courses/course-list/course-list.component';
import { ProfListComponent } from './profs/prof-list/prof-list.component';
import { RoomListComponent } from './rooms/room-list/room-list.component';
import { CourseDetailComponent } from './courses/course-detail/course-detail.component';
import { ProfDetailComponent } from './profs/prof-detail/prof-detail.component';
import { RoomDetailComponent } from './rooms/room-detail/room-detail.component';

import { AppRoutingModule } from './/app-routing.module';

import { CourseService } from './courses/course.service';
import { ProfService } from './profs/prof.service';
import { RoomService } from './rooms/room.service';

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    CourseListComponent,
    ProfListComponent,
    RoomListComponent,
    CourseDetailComponent,
    ProfDetailComponent,
    RoomDetailComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpModule
  ],
  providers: [CourseService, ProfService, RoomService],
  bootstrap: [AppComponent]
})
export class AppModule { }



